import { useEffect, useState } from "react";
import { refundRequestColumns } from "./utiles";
import CustomTable from "@/Shared/Table/CustomTable";
import { SidebarContext } from "@/Context/CustomUsertable";
import { getOrders } from "@/api/ordersManage";
import { fetchUsers } from "../../redux/features/users/usersSlice";
import { useDispatch, useSelector } from "react-redux";
import { Input, Select } from "antd";
import { FiSearch } from "react-icons/fi";
export default function SubscribedUsers() {
  const dispatch = useDispatch();
  const [query, setQuery] = useState();
  const [subscribedUsers, setSubscribedUsers] = useState({
    total: 23,
    data: [
      {
        name: "majharul",
        last_name: "islam",
        email: "example@gmail.com",
      },
    ],
  });
  useEffect(() => {
    dispatch(fetchUsers({ role: "end_users" }));
  }, [dispatch]);
  const users = useSelector((state) => state.users.list || []);
  const status = useSelector((state) => state.users.status);
  const error = useSelector((state) => state.users.error);
  const [loading, setLoading] = useState(false);
  const [page, SetPage] = useState({});
  function getRefundsRequest() {
    // setLoading(true);
    // getOrders({
    //   payment_status: "pending",
    //   is_refund_requested: true,
    //   ...page,
    // }).then((res) => {
    //   setRefundsRequest(res);
    //   console.log(res?.data);
    //   setLoading(false);
    // });
  }
  useEffect(() => {
    getRefundsRequest();
  }, [page]);
  useEffect(() => {
    console.log("============>", users, status, error);
  }, [users]);
  function handleChange() {}
  return (
    <>
      <div
        id="refunds_request"
        className="w-full flex flex-col gap-4 justify-center items-center bg-white rounded-2xl p-6 mt-6"
      >
        <div
          id="Recently_Closed_Alerts_Header"
          className="w-full flex justify-between"
        >
          <Input
            size="large"
            placeholder="Customer name, email..."
            prefix={<FiSearch className="text-gray-400" />}
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            className=" pr-20 border border-gray-300 max-w-[300px]"
            style={{ boxShadow: "none" }}
          />
          <Select
            defaultValue="All"
            style={{ width: 150 }}
            size="large"
            onChange={handleChange}
            options={[
              { value: "All", label: "All Status" },
              { value: "Active", label: "Active" },
              { value: "Canceled", label: "Canceled" },
            ]}
          />
        </div>
        <SidebarContext.Provider
          value={{
            total: subscribedUsers?.total,
            page,
            SetPage,
            getList: getRefundsRequest,
          }}
        >
          <CustomTable
            loading={loading}
            columns={refundRequestColumns}
            tableData={subscribedUsers?.data}
          />
        </SidebarContext.Provider>
      </div>
    </>
  );
}
